<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <!---->
      <el-alert title="" type="warning" show-icon :closable="false"></el-alert>

      <!---->
      <el-row>
        <el-col :span="24">
          <span></span>
          <el-cascader
            expand-trigger="hover"
            :options="categoriesList"
            v-model="selectedCategories"
            @change="handleChange"
            :props="categoriesListProps"
          ></el-cascader>
        </el-col>
      </el-row>

      <!---->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="isDisabled"
            @click="addDialogVisible=true"
          ></el-button>
          <el-table :data="manyTableData" border>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  :key="tag+''+Math.random()"
                  v-for="(tag,i) in scope.row.attr_vals"
                  :disable-transitions="true"
                  closable
                  @close="tagClosed(scope.row,i)"
                >{{tag}}</el-tag>

                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  @click="showEditDialog(scope.row)"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
                <el-button
                  type="danger"
                  @click="delParams(scope.row.attr_id)"
                  icon="el-icon-delete"
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="isDisabled"
            @click="addDialogVisible=true"
          ></el-button>
          <el-table :data="onlyTableData" border>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  :key="tag+''+Math.random()"
                  v-for="tag in scope.row.attr_vals"
                  :disable-transitions="true"
                  closable
                  @close="tagClosed(scope.row,i)"
                >{{tag}}</el-tag>

                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  @click="showEditDialog(scope.row)"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
                <el-button
                  type="danger"
                  @click="delParams(scope.row.attr_id)"
                  icon="el-icon-delete"
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>

      <!---->
      <el-dialog
        :title="''+(activeName==='many'?'':'')"
        :visible.sync="addDialogVisible"
        width="30%"
        @close="addDialogclosed"
      >
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item :label="activeName==='many'?'':''" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addParams"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        :title="''+(activeName==='many'?'':'')"
        :visible.sync="editDialogVisible"
        width="30%"
        @close="editDialogclosed"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item :label="activeName==='many'?'':''" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editParams"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      categoriesList: [],
      // 
      selectedCategories: [],
      // 
      categoriesListProps: {
        value: 'cat_id',
        label: 'cat_name'
      },

      // 
      activeName: 'many',
      //  / 
      manyTableData: [],
      onlyTableData: [],

      /** /form1 */
      addDialogVisible: false,
      addForm: {
        // 
        attr_name: ''
      },
      addFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      /** /form2 */

      /** /form1 */
      // 
      editDialogVisible: false,
      // form
      editForm: {
        // 
        attr_name: ''
      },
      // form
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      /** /form2 */

      /** 1 */
      //    
      inputVisible: false,
      //  
      inputValue: ''
      /** 2 */
    }
  },
  created() {
    // 
    this.getCategoriesList()
  },
  // 
  computed: {
    // 
    isDisabled: function() {
      if (this.selectedCategories.length === 3) {
        return false
      }
      return true
    },
    // id
    catId() {
      if (this.selectedCategories.length === 3) {
        return this.selectedCategories[2]
      }
      return 0
    }
  },
  methods: {
    /** 1 */
    // 
    async tagClosed(nowParam, index) {
      nowParam.attr_vals.splice(index, 1)

      //   
      const attrVals = nowParam.attr_vals.join(' ')
      // axios 
      const { data: res } = await this.$http.put(
        `categories/${nowParam.cat_id}/attributes/${nowParam.attr_id}`,
        {
          attr_name: nowParam.attr_name,
          attr_sel: nowParam.attr_sel,
          attr_vals: attrVals
        }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
      }
    },

    //    
    async handleInputConfirm(nowParam) {
      // 
      if (this.inputValue.trim().length === 0) {
        this.inputValue = ''
        this.inputVisible = false
        return null
      }
      // console.log(123)
      //  attr_vals
      nowParam.attr_vals.push(this.inputValue.trim())
      // 
      this.inputValue = ''
      // 
      this.inputVisible = false

      //   
      const attrVals = nowParam.attr_vals.join(' ')
      // axios 
      const { data: res } = await this.$http.put(
        `categories/${nowParam.cat_id}/attributes/${nowParam.attr_id}`,
        {
          attr_name: nowParam.attr_name,
          attr_sel: nowParam.attr_sel,
          attr_vals: attrVals
        }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
      }
    },
    //   
    showInput() {
      // 
      this.inputVisible = true

      //  
      // this.$nextTick()
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    /** 2 */

    /** 1 */
    async delParams(attrId) {
      const cfm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete(
          `categories/${this.catId}/attributes/${attrId}`
        )

        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }

        // 
        this.getParamsByCate()
        // 
        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
      }
    },
    /** 2 */

    /** /form1 */
    // 
    // @param info 
    async showEditDialog(info) {
      //  
      this.editForm = _.cloneDeep(info)
      // 
      this.editDialogVisible = true
    },
    // 
    editDialogclosed() {},
    // 
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put(
            `categories/${this.editForm.cat_id}/attributes/${
              this.editForm.attr_id
            }`,
            {
              attr_name: this.editForm.attr_name,
              attr_sel: this.activeName
            }
          )

          if (res.meta.status !== 200) {
            return this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }

          // 
          this.getParamsByCate()
          // 
          this.editDialogVisible = false
          // 
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
        }
      })
    },
    /** /form2 */

    /** /form1 */
    // 
    addDialogclosed() {
      // form
      this.$refs.addFormRef.resetFields()
      // 
      this.addDialogVisible = false
    },
    // 
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            `categories/${this.catId}/attributes`,
            {
              attr_name: this.addForm.attr_name,
              attr_sel: this.activeName
            }
          )
          if (res.meta.status !== 201) {
            return this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }
          // 
          this.getParamsByCate()
          // 
          this.addDialogVisible = false
          // 
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
        }
      })
    },
    /** /form2 */

    // tab
    handleClick(tab, event) {
      if (!this.catId) return null
      //    
      this.getParamsByCate()
    },
    // 
    handleChange() {
      if (this.selectedCategories.length < 3) {
        // 
        this.selectedCategories = []
        //    
        this.manyTableData = []
        this.onlyTableData = []
      } else {
        //    
        this.getParamsByCate()
      }
    },
    //      
    async getParamsByCate() {
      //    
      const { data: res } = await this.$http.get(
        `categories/${this.catId}/attributes`,
        { params: { sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }

      //   
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
      })
      // console.log(res.data)

      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    async getCategoriesList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }

      // console.log(res.data)
      this.categoriesList = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.el-alert {
  margin-bottom: 15px;
}
.el-tag {
  margin: 10px 5px;
}
.input-new-tag {
  width: 110px;
}
</style>



