<template>
  <div class="userlist-container">
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
            @keyup.enter.native="getUserList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="userList" stripe style="width: 100%" border>
        <el-table-column type="index" label="" width="50"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="mobile" label="" width="120"></el-table-column>
        <el-table-column prop="role_name" label="" width="180"></el-table-column>
        <el-table-column prop="email" label="" width="180"></el-table-column>
        <el-table-column prop="mg_state" label="" width="70">
          <!--row-->
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="changeState(scope.row.id,scope.row.mg_state)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop label="" width="200">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delUser(scope.row.id)"
            ></el-button>
            <el-tooltip placement="top" :enterable="false">
              <div slot="content"></div>
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="showRoleDialogVisible(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>

      <!---->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        top="185px"
        :before-close="addDialogClose"
      >
        <!---->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        top="185px"
        :before-close="editDialogCloseBefore"
      >
        <!---->
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="editForm.username" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogClose"> </el-button>
          <el-button type="primary" @click="editUser"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        title=""
        :visible.sync="roleDialogVisible"
        width="50%"
        top="185px"
        :before-close="roleDialogCloseBefore"
      >
        <!---->
        <el-form
          :model="roleForm"
          :rules="roleFormRules"
          ref="roleFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <span>{{roleForm.username}}</span>
          </el-form-item>
          <el-form-item label="" prop="role_name">
            <span>{{roleForm.role_name}}</span>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="newUserRoleId" placeholder="" clearable ref="abc">
              <el-option
                v-for="item in rolesList"
                :key="item.id"
                :label="item.roleName"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="roleDialogClose"> </el-button>
          <el-button type="primary" @click="roleUser"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import mix from './UserList-mixins.js'
export default {
  mixins: [mix],
  data() {
    return {}
  }
}
</script>

<style lang="less" scoped>
</style>



