<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <!---->
      <el-table :data="rightsList" stripe style="width: 100%" border>
        <el-table-column type="index" label="" width="170"></el-table-column>
        <el-table-column prop="authName" label="" width="240"></el-table-column>
        <el-table-column prop="path" label="" width="200"></el-table-column>
        <el-table-column prop="level" label="" width="*">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level == 0"></el-tag>
            <el-tag v-else-if="scope.row.level == 1" type="success"></el-tag>
            <el-tag v-else type="warning"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import mix from './Rights-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
</style>



