<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="search">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="goodsList" stripe style="width: 100%" border>
        <el-table-column type="index" label="" width="50"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="100"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="90"></el-table-column>
        <el-table-column label="" width="140">
          <template slot-scope="scope">
            <span>{{scope.row.add_time|dateFormat}}</span>
          </template>
        </el-table-column>
        <el-table-column prop label="" width="150">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delGoods(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      goodsList: [], // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async delGoods(goodsId) {
      const cfm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete('goods/' + goodsId)

        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }

        // 
        this.getGoodsList()
        // 
        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
      }
    },

    // 
    goAddPage() {
      console.log(this)
      this.$router.push('goodsadd')
    },
    // 
    search() {
      this.queryInfo.pagenum = 1
      this.getGoodsList()
    },

    // 
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      this.getGoodsList()
    },

    // 
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getGoodsList()
    },

    // 
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      // console.log(res)
      this.goodsList = res.data.goods
      this.total = res.data.total
    }
  }
}
</script>

<style lang="less" scoped>
</style>



