export default {
  data() {
    return {
      // 
      menulist: [],
      // 
      iconlist: [
        'icon-users',
        'icon-tijikongjian',
        'icon-shangpin',
        'icon-danju',
        'icon-baobiao'
      ],
      // ,false:  true:
      leftshow: false
    }
  },
  created() {
    this.getMenuList()
  },
  methods: {
    // 
    async getMenuList() {
      const { data: res } = await this.$http.get('/menus')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.menulist = res.data
    },
    // 
    logout() {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          window.sessionStorage.removeItem('token')
          this.$router.push('/login')
        })
        .catch(() => {})
    }
  }
}



