<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/goods'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>

      <!---->
      <el-steps :active="activeName-0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!---->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-tabs
          v-model="activeName"
          @tab-click="tabClicked"
          tab-position="left"
          :before-leave="tabBeforeLeave"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                expand-trigger="hover"
                :options="catList"
                v-model="addForm.goods_cat"
                :props="catProps"
                @change="catChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyParams" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="item2"
                  :key="i2+''+Math.random()"
                  v-for="(item2,i2) in item.attr_vals"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyParams"
              :key="item.attr_id"
              label-width="150px"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              :action="uploadUrl"
              :on-preview="uploadPreview"
              :on-remove="uploadRemove"
              :on-success="uploadSuccess"
              list-type="picture"
              :headers="uploadParam"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce" ref="myQuillEditor"></quill-editor>
            <el-button type="primary" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>

      <!--  -->
      <el-dialog title="" :visible.sync="previewVisible" width="50%">
        <img :src="previewImg" alt class="img-preview">
      </el-dialog>
      <!--end-->
    </el-card>
  </div>
</template>

<script>
import config from '../../config.json'

// 
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'

// lodash
import _ from 'lodash'

export default {
  components: {
    // 
    quillEditor
  },
  data() {
    return {
      //  steps 
      activeName: '0',

      /** 1 */
      addForm: {
        goods_name: '',
        goods_price: '',
        goods_weight: '',
        goods_number: '',
        goods_cat: [],
        pics: [],
        attrs: []
      },
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },

      // 
      catList: [],
      // 
      catProps: {
        value: 'cat_id',
        label: 'cat_name'
      },
      // id
      threeCatId: 0,
      // 
      manyParams: [],
      // 
      onlyParams: [],
      // 
      uploadUrl: config.baseURL + 'upload',
      // 
      uploadParam: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewImg: '',
      // dialog
      previewVisible: false
      /** 2 */
    }
  },
  created() {
    this.getCatList()
  },
  methods: {
    // 
    addGoods() {
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          // addForm
          var form = _.cloneDeep(this.addForm)
          form.goods_cat = form.goods_cat.join(',')

          //   
          this.manyParams.forEach(item => {
            if (item.attr_vals.length > 0) {
              var newobj = {
                attr_id: item.attr_id,
                attr_value: item.attr_vals.join(' ')
              }
              form.attrs.push(newobj)
            }
          })

          //   
          this.onlyParams.forEach(item => {
            if (item.attr_vals.trim()) {
              var newobj = { attr_id: item.attr_id, attr_value: item.attr_vals }
              form.attrs.push(newobj)
            }
          })

          // 
          const { data: res } = await this.$http.post('goods', form)

          if (res.meta.status !== 201) {
            return this.$message({
              message: res.meta.msg,
              type: 'error',
              duration: 1500
            })
          }

          // 
          this.$message({
            message: res.meta.msg,
            type: 'success',
            duration: 1500
          })
          // 
          this.$router.push('goods')
          // console.log(form)
        }
      })
    },

    // 
    uploadSuccess(result) {
      if (result.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
      }

      // console.log(result)
      // addForm.pics
      var o = { pic: result.data.tmp_path }
      this.addForm.pics.push(o)
    },
    // 
    uploadPreview(result) {
      // 
      this.previewImg = result.response.data.url
      // dialog
      this.previewVisible = true
    },
    // 
    uploadRemove(result) {
      if (result.response.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
      }

      // pics
      var i = this.addForm.pics.findIndex(item => {
        return item.pic === result.response.data.tmp_path
      })
      // pics
      this.addForm.pics.splice(i, 1)
    },

    //  
    async tabClicked(tab, event) {
      // 
      if (this.activeName === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.threeCatId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1500
          })
        }

        // attr_vals 
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        })

        this.manyParams = res.data
      } else if (this.activeName === '2') {
        // 
        const { data: res } = await this.$http.get(
          `categories/${this.threeCatId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1500
          })
        }
        // console.log(res)
        this.onlyParams = res.data
      }
    },
    // 
    async getCatList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
      }
      // console.log(res.data)
      this.catList = res.data
    },

    // 
    catChange() {
      // 
      if (this.addForm.goods_cat.length === 3) {
        // console.log(this.addForm)
        // id
        this.threeCatId = this.addForm.goods_cat[2]
      } else {
        // 
        this.threeCatId = 0
        this.addForm.goods_cat = []
      }
    },

    // 
    tabBeforeLeave(newTab, oldTab) {
      if (oldTab === '0' && this.threeCatId === 0) {
        this.$message({
          message: '',
          type: 'error',
          duration: 1500
        })
        return false
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-steps,
.el-tabs {
  margin-top: 20px;
}
// img
.img-preview {
  width: 100%;
}
.quill-editor {
  margin-bottom: 15px;
}
</style>



