<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <el-button type="primary" @click="showAddDialog()"></el-button>
      <table-tree
        :data="categoriesList"
        :columns="categoriesListColumns"
        :selection-type="false"
        :expand-type="false"
        show-index
      >
        <template slot="deleted" slot-scope="scope">
          <i
            class="el-icon-circle-check"
            style="color:rgb(68, 178, 170)"
            v-if="scope.row.cat_deleted===false"
          ></i>
          <i class="el-icon-circle-close" style="color:red;" v-else></i>
        </template>
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level===0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level===1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDialog(scope.row.cat_id)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="delCate(scope.row.cat_id)"
          ></el-button>
        </template>
      </table-tree>

      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-size="queryInfo.pagesize"
        layout="total,  prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <!---->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addForm.cat_name"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-cascader
              expand-trigger="hover"
              :options="cateTwoList"
              :props="cateTwoListProps"
              v-model="selectedCateTwo"
              @change="cateTwoChange"
              change-on-select
              clearable
              style="width:100%"
            ></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCate"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editForm.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editCate"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      categoriesList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      /** table-tree 1 */
      categoriesListColumns: [
        { label: '', prop: 'cat_name', width: '200px' },
        {
          label: '',
          prop: 'cat_deleted',
          width: '200px',
          type: 'template',
          template: 'deleted'
        },
        {
          label: '',
          prop: 'cat_level',
          width: '200px',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          prop: 'cat_deleted',
          width: '200px',
          type: 'template',
          template: 'opt'
        }
      ],
      /** table-tree 2 */

      /** 1 */
      addDialogVisible: false,
      // 
      addForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      // 
      addFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cateTwoList: [],
      // ""
      cateTwoListProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCateTwo: [],
      /** 2 */

      /** 1 */
      editDialogVisible: false,
      // 
      editForm: {
        cat_name: ''
      },
      // 
      editFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
      /** 2 */
    }
  },
  created() {
    this.getCategoriesList()
  },
  methods: {
    /** 1 */
    async delCate(catId) {
      const cfm = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete('categories/' + catId)
        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }

        // 
        this.getCategoriesList()
        // 
        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
      }
    },
    /** 2 */

    /** 1 */
    // 
    async showEditDialog(catId) {
      // cat_id
      const { data: res } = await this.$http.get('categories/' + catId)
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }

      // editForm
      this.editForm = res.data
      this.editDialogVisible = true
    },

    //    
    editCate() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put(
            'categories/' + this.editForm.cat_id,
            this.editForm
          )

          if (res.meta.status !== 200) {
            return this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }

          // 
          this.editDialogVisible = false
          // 
          this.getCategoriesList()
          // 
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
        }
      })
    },

    //   
    editDialogClosed() {
      // 
      this.$refs.editFormRef.resetFields()
    },
    /** 2 */

    /** 1 */
    // 
    addCate() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            'categories',
            this.addForm
          )
          if (res.meta.status !== 201) {
            return this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }

          // 
          this.addDialogVisible = false
          // 
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
          // 
          this.getCategoriesList()
        }
      })
    },

    // Dialog
    async showAddDialog() {
      // 
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.cateTwoList = res.data
      this.addDialogVisible = true
    },

    //  
    addDialogClosed() {
      // 
      this.$refs.addFormRef.resetFields()
      // 
      this.selectedCateTwo = []
      // // 
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    },

    // 
    cateTwoChange() {
      //  addForm.cat_pid
      if (this.selectedCateTwo.length === 0) {
        // id
        this.addForm.cat_pid = 0
        // 
        this.addForm.cat_level = 0
      } else {
        var len = this.selectedCateTwo.length
        this.addForm.cat_pid = this.selectedCateTwo[len - 1]
        this.addForm.cat_level = len
      }
    },
    /** 2 */

    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCategoriesList()
    },
    // 
    async getCategoriesList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.categoriesList = res.data.result
      this.total = res.data.total
    }
  }
}
</script>

<style lang="less" scoped>
// table-tree
.zk-table {
  margin-top: 15px;
}
</style>



