<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <!---->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="orderList" border scripe>
        <el-table-column type="index" width="70px"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="90px"></el-table-column>
        <el-table-column label="" prop="order_pay" width="100px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.order_pay==''"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="100px"></el-table-column>
        <el-table-column label="" prop="create_time" width="200px">
          <template slot-scope="scope">
            <span>{{scope.row.create_time|dateFormat}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="140px">
          <template slot-scope="scope">
            <el-button icon="el-icon-edit" size="mini" type="primary" title=""></el-button>
            <el-button
              icon="el-icon-location"
              size="mini"
              type="success"
              title=""
              @click="showWuliuDialog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <!---->
      <el-dialog title="" :visible.sync="wuliuDialogVisible" width="50%">
        <el-steps direction="vertical" :active="0">
          <el-step
            :key="i"
            v-for="(item,i) in wuliuList"
            :title="item.time"
            :description="item.context"
          ></el-step>
        </el-steps>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      orderList: [],
      // 
      queryInfo: {
        query: '', // 
        pagenum: 1, // 
        pagesize: 5 // 
      },
      total: 0, // 
      wuliuDialogVisible: false, // 
      wuliuList: [] // 
    }
  },
  created() {
    this.getOrderList()
  },
  // filters: {
  //   // 
  //   dateFormat(val) {
  //     var dt = new Date(val)
  //     var yy = dt.getFullYear()
  //     var mm = (dt.getMonth() + 1 + '').padStart(2, 0)
  //     var dd = (dt.getDate() + '').padStart(2, 0)

  //     var hh = (dt.getHours() + '').padStart(2, 0)
  //     var ii = (dt.getMinutes() + '').padStart(2, 0)
  //     var ss = (dt.getSeconds() + '').padStart(2, 0)
  //     return `${yy}-${mm}-${dd} ${hh}:${ii}:${ss}`
  //   }
  // },
  methods: {
    // 
    async showWuliuDialog() {
      // axios
      const { data: res } = await this.$http.get('kuaidi/81337523816')

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }

      this.wuliuList = res.data
      this.wuliuDialogVisible = true
    },

    // 
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      this.getOrderList()
    },

    // 
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getOrderList()
    },

    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.total = res.data.total
      this.orderList = res.data.goods
    }
  }
}
</script>

<style lang="less" scoped>
</style>



