export default {
  data() {
    var checkMobile = (rule, value, callback) => {
      if (!value.trim()) {
        return callback(new Error(''))
      }
      if (!/^1[34578]\d{9}$/.test(value)) {
        return callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      // 
      userList: [],
      // 
      queryInfo: {
        // 
        query: '',
        // n
        pagenum: 1,
        // 
        pagesize: 2,
        // 
        total: 0
      },

      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }],
        mobile: [{ validator: checkMobile, trigger: 'blur' }]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {
        username: '',
        email: '',
        mobile: ''
      },
      editFormRules: {
        email: [{ required: true, message: '', trigger: 'blur' }],
        mobile: [{ validator: checkMobile, trigger: 'blur' }]
      },

      /** 1 */
      //   
      roleDialogVisible: false,
      // 
      roleForm: {
        username: '',
        role_name: ''
      },
      roleFormRules: {},
      // 
      rolesList: [],
      // 
      newUserRoleId: ''
      /** 2 */
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    /** 1 */
    // -
    async showRoleDialogVisible(userinfo) {
      this.roleDialogVisible = true
      //  
      this.roleForm = userinfo
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.rolesList = res.data
    },
    // 
    async roleUser() {
      if (!this.newUserRoleId) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      const { data: res } = await this.$http.put(
        'users/' + this.roleForm.id + '/role',
        { rid: this.newUserRoleId }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      // 
      this.roleDialogClose()
      // 
      this.getUserList()
      // 
      this.$message({
        message: '',
        type: 'success',
        duration: 1000
      })
    },
    // ()
    roleDialogCloseBefore(done) {
      // 
      // this.$refs.roleFormRef.resetFields()
      this.newUserRoleId = ''
      // 
      done()
    },
    // 
    roleDialogClose() {
      // 
      this.newUserRoleId = ''
      // 
      this.roleDialogVisible = false
    },
    /** 2 */

    // 
    async delUser(id) {
      const cfm = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete('users/' + id)

        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }

        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })

        // 
        if (this.userList.length === 1 && this.queryInfo.pagenum > 1) {
          this.queryInfo.pagenum--
        }
        // 
        this.getUserList()
      }
    },
    // 
    addUser() {
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return null
        }

        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
        // form
        this.$refs.addFormRef.resetFields()
        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
      })
    },

    // 
    async changeState(id, state) {
      const { data: res } = await this.$http.put(`users/${id}/state/${state}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message({
        message: '',
        type: 'success',
        duration: 1500
      })
    },
    // 
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userList = res.data.users
      // 
      this.queryInfo.total = res.data.total
    },

    // 
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // n
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    addDialogClose(done) {
      // 
      this.$refs.addFormRef.resetFields()
      // 
      done()
    },
    // 
    editUser() {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return null
        }

        // 
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          this.editForm
        )
        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // form
        this.$refs.editFormRef.resetFields()
        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
      })
    },
    //   
    async showEditDialog(id) {
      // id
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      // form
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // ()
    editDialogCloseBefore(done) {
      // 
      this.$refs.editFormRef.resetFields()
      // 
      done()
    },
    // 
    editDialogClose() {
      // 
      this.$refs.editFormRef.resetFields()
      // 
      this.editDialogVisible = false
    }
  }
}



