<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <el-button type="primary" @click="addDialogVisible=true"></el-button>
      <!---->
      <el-table :data="rolesList" border scripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1,k1) in scope.row.children"
              :key="item1.id"
              :style="{ 'border-bottom': '1px solid rgb(235,238,245)','border-top':k1===0?'1px solid rgb(235,238,245)':'' }"
            >
              <el-col :span="5">
                <el-tag closable @close="delRight(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  v-for="(item2,k2) in item1.children"
                  :key="item2.id"
                  :style="{ 'border-top': k2!==0?'1px solid rgb(235,238,245)':''}"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="delRight(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      closable
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="warning"
                      @close="delRight(scope.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delRole(scope.row.id)"
            ></el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" top="185px">
        <el-tree
          :data="rightsList"
          :props="rightsListProps"
          show-checkbox
          node-key="id"
          default-expand-all
          :default-checked-keys="defaultCheckedKeys"
          ref="rightRef"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightDialogVisible=false"> </el-button>
          <el-button type="primary" @click="setRight"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        top="185px"
        :before-close="addDialogCloseBefore"
        @close="addDialogClose"
      >
        <!---->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogClose"> </el-button>
          <el-button type="primary" @click="addRole"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        top="185px"
        :before-close="editDialogCloseBefore"
        @close="editDialogClose"
      >
        <!---->
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogClose"> </el-button>
          <el-button type="primary" @click="editRole"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      /** 1 */
      // 
      setRightDialogVisible: false,
      // 
      rightsList: [],
      rightsListProps: {
        label: 'authName',
        children: 'children'
      },
      // 3 ids
      defaultCheckedKeys: [],
      // id
      roleId: '',
      /** 2 */

      // 
      rolesList: [],
      //  
      addDialogVisible: false,
      // 
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      //  
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //  
      editDialogVisible: false,
      // 
      editForm: {
        roleName: '',
        roleDesc: ''
      },
      //  
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    /** 1 */
    // 
    async showSetRightDialog(role) {
      this.setRightDialogVisible = true
      this.roleId = role.id
      // tree
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message({
          message: 'tree',
          type: 'error',
          duration: 1000
        })
      }
      this.rightsList = res.data
      // id
      const keys = []
      this.getLeafIds(role, keys)
      this.defaultCheckedKeys = keys
    },
    // ids
    // nodekeyArr
    getLeafIds(node, keyArr) {
      if (!node.children) {
        return keyArr.push(node.id)
      }
      node.children.forEach(item => this.getLeafIds(item, keyArr))
    },
    // 
    async setRight() {
      // id
      const k1 = this.$refs.rightRef.getCheckedKeys()
      // id
      const k2 = this.$refs.rightRef.getHalfCheckedKeys()

      // ids
      const idsstr = [...k1, ...k2].join(',')

      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idsstr }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }

      this.$message({
        message: '',
        type: 'success',
        duration: 1000
      })
      // 
      this.getRolesList()
      this.setRightDialogVisible = false
    },
    /** 2 */

    /** 1 */
    // id
    async delRight(role, rightid) {
      const cfm = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete(
          `roles/${role.id}/rights/${rightid}`
        )
        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        }

        this.$message({
          message: '',
          type: 'success',
          duration: 1000
        })
        // 
        role.children = res.data
      }
    },
    /** 2 */

    /** ******1*********/
    async delRole(id) {
      const cfm = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete('roles/' + id)
        if (res.meta.status !== 200) {
          return this.$message({
            message: '',
            type: 'error',
            duration: 1000
          })
        } else {
          this.getRolesList()
          return this.$message({
            message: '',
            duration: 1000
          })
        }
      }
    },
    /** ******2*********/
    /** ******1*********/
    // 
    async showEditDialog(id) {
      // 
      this.editDialogVisible = true
      // 
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.editForm = res.data
    },
    // 
    //   
    editDialogCloseBefore(done) {
      this.$refs.editFormRef.resetFields()
      done()
    },
    //   
    editDialogClose() {
      this.editDialogVisible = false
      this.$refs.editFormRef.resetFields()
    },
    // 
    editRole() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put(
            'roles/' + this.editForm.roleId,
            this.editForm
          )

          if (res.meta.status !== 200) {
            this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }

          // 
          this.editDialogClose()
          // 
          this.getRolesList()
          // 
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
        }
      })
    },
    /** ******2*********/
    // 
    //   
    addDialogCloseBefore(done) {
      this.$refs.addFormRef.resetFields()
      done()
    },
    //   
    addDialogClose() {
      this.addDialogVisible = false
      this.$refs.addFormRef.resetFields()
    },
    //  
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post('roles', this.addForm)
          if (res.meta.status !== 201) {
            return this.$message({
              message: '',
              type: 'error',
              duration: 1000
            })
          }
          // 
          this.addDialogClose()
          // 
          this.getRolesList()
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          })
        }
      })
    },
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message({
          message: '',
          type: 'error',
          duration: 1000
        })
      }
      this.rolesList = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px 5px;
}
.el-row{
  display:flex;
  align-items:center;
}
</style>



